<template>
	<div class="addEdit-block">
		<el-form
			class="add-update-preview"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="180px"
		>
			<template >
				<el-form-item class="input" v-if="type!='info'"  label="项目名称" prop="xiangmumingcheng" >
					<el-input v-model="ruleForm.xiangmumingcheng" placeholder="项目名称" clearable  :readonly="ro.xiangmumingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="项目名称" prop="xiangmumingcheng" >
					<el-input v-model="ruleForm.xiangmumingcheng" placeholder="项目名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.xiangmufengmian" label="项目封面" prop="xiangmufengmian" >
					<file-upload
						tip="点击上传项目封面"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.xiangmufengmian?ruleForm.xiangmufengmian:''"
						@change="xiangmufengmianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.xiangmufengmian" label="项目封面" prop="xiangmufengmian" >
					<img v-if="ruleForm.xiangmufengmian.substring(0,4)=='http'&&ruleForm.xiangmufengmian.split(',w').length>1" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.xiangmufengmian" width="100" height="100">
					<img v-else-if="ruleForm.xiangmufengmian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.xiangmufengmian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.xiangmufengmian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="发放资金" prop="fafangzijin" >
					<el-input-number v-model="ruleForm.fafangzijin" placeholder="发放资金" :disabled="ro.fafangzijin"></el-input-number>
				</el-form-item>
				<el-form-item v-else class="input" label="发放资金" prop="fafangzijin" >
					<el-input v-model="ruleForm.fafangzijin" placeholder="发放资金" readonly></el-input>
				</el-form-item>
				<el-form-item class="date" v-if="type!='info'" label="发放时间" prop="fafangshijian" >
					<el-date-picker
						format="yyyy 年 MM 月 dd 日"
						value-format="yyyy-MM-dd"
						v-model="ruleForm.fafangshijian" 
						type="date"
						:readonly="ro.fafangshijian"
						placeholder="发放时间"
					></el-date-picker> 
				</el-form-item>
				<el-form-item class="input" v-else-if="ruleForm.fafangshijian" label="发放时间" prop="fafangshijian" >
					<el-input v-model="ruleForm.fafangshijian" placeholder="发放时间" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="企业账号" prop="qiyezhanghao" >
					<el-input v-model="ruleForm.qiyezhanghao" placeholder="企业账号" clearable  :readonly="ro.qiyezhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="企业账号" prop="qiyezhanghao" >
					<el-input v-model="ruleForm.qiyezhanghao" placeholder="企业账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="负责人姓名" prop="fuzerenxingming" >
					<el-input v-model="ruleForm.fuzerenxingming" placeholder="负责人姓名" clearable  :readonly="ro.fuzerenxingming"></el-input>
				</el-form-item>
