					type="textarea"
					:rows="8"
					placeholder="发放内容"
					v-model="ruleForm.fafangneirong" >
				</el-input>
			</el-form-item>
			<el-form-item v-else-if="ruleForm.fafangneirong" label="发放内容" prop="fafangneirong" >
				<span class="text">{{ruleForm.fafangneirong}}</span>
			</el-form-item>
			<el-form-item v-if="type!='info'"  label="发放详情" prop="fafangxiangqing" >
