<script>
	import { 
		isNumber,
	} from "@/utils/validate";
	export default {
		data() {
			var validateNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isNumber(value)) {
					callback(new Error("请输入数字"));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					xiangmumingcheng : false,
					xiangmufengmian : false,
					fafangzijin : false,
					fafangshijian : false,
					fafangneirong : false,
					fafangxiangqing : false,
					qiyezhanghao : false,
					fuzerenxingming : false,
				},
			
				ruleForm: {
					xiangmumingcheng: '',
					xiangmufengmian: '',
					fafangzijin: '',
					fafangshijian: '',
					fafangneirong: '',
					fafangxiangqing: '',
					qiyezhanghao: '',
					fuzerenxingming: '',
				},

				rules: {
					xiangmumingcheng: [
					],
					xiangmufengmian: [
					],
					fafangzijin: [
						{ validator: validateNumber, trigger: 'blur' },
					],
					fafangshijian: [
					],
					fafangneirong: [
					],
					fafangxiangqing: [
					],
					qiyezhanghao: [
					],
					fuzerenxingming: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
			this.ruleForm.fafangshijian = this.getCurDate()
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'||this.type=='msg'){
					this.info(id);
				}else if(this.type=='logistics'){
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='xiangmumingcheng'){
							this.ruleForm.xiangmumingcheng = obj[o];
							this.ro.xiangmumingcheng = true;
							continue;
						}
						if(o=='xiangmufengmian'){
							this.ruleForm.xiangmufengmian = obj[o];
							this.ro.xiangmufengmian = true;
							continue;
						}
						if(o=='fafangzijin'){
							this.ruleForm.fafangzijin = obj[o];
							this.ro.fafangzijin = true;
							continue;
						}
						if(o=='fafangshijian'){
							this.ruleForm.fafangshijian = obj[o];
							this.ro.fafangshijian = true;
							continue;
						}
						if(o=='fafangneirong'){
							this.ruleForm.fafangneirong = obj[o];
							this.ro.fafangneirong = true;
							continue;
						}
						if(o=='fafangxiangqing'){
							this.ruleForm.fafangxiangqing = obj[o];
							this.ro.fafangxiangqing = true;
							continue;
						}
						if(o=='qiyezhanghao'){
							this.ruleForm.qiyezhanghao = obj[o];
							this.ro.qiyezhanghao = true;
							continue;
						}
						if(o=='fuzerenxingming'){
							this.ruleForm.fuzerenxingming = obj[o];
							this.ro.fuzerenxingming = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `fafangbutie/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
						this.ruleForm.fafangxiangqing = this.ruleForm.fafangxiangqing.replace(reg,'../../../GZLS_IMS/upload');
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					if(this.ruleForm.xiangmufengmian!=null) {
						this.ruleForm.xiangmufengmian = this.ruleForm.xiangmufengmian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `fafangbutie/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.fafangbutieCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.fafangbutieCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			xiangmufengmianUploadChange(fileUrls) {
				this.ruleForm.xiangmufengmian = fileUrls;
			},
		}
	};
</script>
